import React, { Component } from 'react';

export class NewsItem extends Component {
    
  render() {
      let {title, description, imageUrl, newsUrl, author, date, source} = this.props
    return (
        <div className="my-3">
            <div className="card" style={{width: "20rem",height: "26rem"}}>
            <span class="position-absolute start-50 translate-middle badge rounded-pill bg-danger" style={{position:"absolute", top:10}}>{source}</span>
                <img src={imageUrl} className="card-img-top" style={{height: "11rem"}} alt="..."/>
                <div className="card-body">
                    <h5 className="card-title">{title}.. <span className='badge rounded-pill bg-success'>New</span></h5>
                    <p className="card-text">{description}..</p>
                    <p><small>By {author} On {date}</small></p>
                    <a href={newsUrl} rel="noreferrer" target="_blank" class="btn btn-sm btn-danger" style={{position:"absolute", bottom:0, right:0}}>Read More</a>
                </div>
            </div>
        </div>
    )
  }
}

export default NewsItem;
