import React, { Component} from 'react';
import NewsItem from './NewsItem';
import Spinner from './Spinner';
import PropTypes from 'prop-types'


export class News extends Component {
    static defaultProps = {
      country:"in",
      pageSize:6,
      category:"general"
    }
    NewsPropTypes = {
      country:PropTypes.string,
      pageSize:PropTypes.number,
      category:PropTypes.string
    }
    capitalizeLetter = (string)=>{
      return string.charAt(0).toUpperCase() + string.slice(1);

    }
    constructor(props){
        super(props);
        this.state={
            articles:[],
            loading:false,
            page:1,
            // totalResults:20
        }
        document.title = `${this.capitalizeLetter(this.props.category)} - DailyNews`
    }

    async updateNews(){
      let url=`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=1516e67bfde1436c9bf48981430341c9&page=${this.state.page}&pageSize=${this.props.pageSize}`
      this.setState({loading:true})
      let data = await fetch(url)
      let parseData = await data.json()
      this.setState({
        articles:parseData.articles,
        totalResults:parseData.totalResults,
        loading:false
      })
    }

    async componentDidMount(){
      this.updateNews()
    }
    handleNextClick = async ()=>{
      this.setState({page:this.state.page+1})
      this.updateNews()
    }

    handlePrevClick = async ()=>{
      this.setState({page:this.state.page-1})
      this.updateNews()
    }
  render() {
    return (
        <div className='container my-3'>
            <h2 className='text-center' style={{marginTop:"90px"}}>DailyNews - Top {this.capitalizeLetter(this.props.category)} Headlines</h2>
            {this.state.loading && <Spinner/>}
              <div className="row">
                {!this.state.loading && this.state.articles.map((element)=>{
                return <div className="col-md-4" key={element.url}>
                  <NewsItem title={element.title?element.title.slice(0,45):""}  description={element.description?element.description.slice(0,72):""} imageUrl={element.urlToImage?element.urlToImage:"https://ichef.bbci.co.uk/live-experience/cps/624/cpsprodpb/A464/production/_121148024_pattinson_getty.jpg"} newsUrl={element.url} author={element.author?element.author:"Unknown"} date={(new Date(element.publishedAt)).toLocaleString()} source={element.source.name}/>
                  </div>
                })}
            </div>
            <div className="container d-flex justify-content-between">
              <button disabled={this.state.page<=1} type="button" className="btn btn-success" onClick={this.handlePrevClick}>&larr; Previous</button>
              <button disabled={this.state.page+1 > Math.ceil(this.state.totalResults/this.props.pageSize)} type="button" className="btn btn-success" onClick={this.handleNextClick}>Next &rarr;</button>
            </div>
        </div>
    )
  }
}

export default News;
